:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.button-container {
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.load-more .load-more-hidden {
  display: none;
}

.load-more, .search-btn {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more-hidden {
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.gallery {
  max-width: 1140px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 200px;
  justify-content: center;
  gap: 65px 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  text-align: center;
  align-items: center;
  margin-right: auto;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-bottom: 50px;
  display: flex;
}

input {
  padding: 10px 20px;
  font-size: 1.2em;
  display: inline-block;
}

